.toolbar {
  top: 0;
  left: 0;
  width: 100%;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);

  height: 100px;
}

.toolbar__navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 02rem;
}

.toolbar__logo {
  margin-left: 1rem;
}

.toolbar__logo a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
  font-family: "Gayathri", sans-serif;
}

.spacer {
  flex: 1;
}

.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.toolbar_navigation-items li {
  padding: 0 0.5rem;
  margin-right: 50px;
}

.toolbar_navigation-items a {
  color: #e2d1c3;
  text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: #a73737;
}

@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }

  .toolbar__logo {
    margin-left: 0;
  }
}
